<template>
  <div>
    <!--  -->
    <breadcrumb-nav>
      <template v-slot:firstMenu></template>
      <template v-slot:secondMenu></template>
    </breadcrumb-nav>

    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <order-table :order-list="orderList" @order-list="updateOrderList" />

      <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="queryInfo.pagenum"
              :page-sizes="[5,10,15,20]"
              :page-size="queryInfo.pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
  import BreadcrumbNav from "../common/BreadcrumbNav";

  // 
  import OrderTable from "./orderChildComponents/OrderTable";
  import OrderEdit from "./orderChildComponents/OrderEdit";
  import OrderProgress from "./orderChildComponents/OrderProgress";

  import {getOrderListRequest} from 'network/order';

  // 
  import cityData from "./orderData/citydata";

  export default {
    name: "Order",
    components: {
      BreadcrumbNav,
      OrderTable,
      OrderEdit,
      OrderProgress
    },
    data() {
      return {
        queryInfo: {
          query: '',
          pagenum: 1,
          pagesize: 10
        },
        orderList: [],
        total: 0,
        cityData
      }
    },
    created() {
      this.getOrderList();
    },
    methods: {
      getOrderList() {
        getOrderListRequest(this.queryInfo).then(res => {
          let result = res.data;
          if (result.meta.status !== 200) {
            return this.alertMessage('', 'error');
          }
          this.orderList = result.data.goods;
          this.total = result.data.total;
        })
      },

      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize;
        this.getOrderList();
      },

      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage;
        this.getOrderList();
      },

      // 
      updateOrderList(){
        this.getOrderList();
      }
    }
  }
</script>

<style scoped>
  .el-cascader {
    width: 100%;
  }
</style>
