<template>
  <div class="editDialog">
    <!--  -->
    <el-button type="primary" size="mini" icon="el-icon-edit" @click="showBox(orderId)"></el-button>

    <!--  -->
    <el-dialog
            title=""
            :visible.sync="editDialogVisible"
            width="50%"
            @close="addressDialogClose">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="" prop="order_price">
          <el-input v-model="editForm.order_price" type="number" min="0"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="editForm.pay_status" label="1"></el-radio>
          <el-radio v-model="editForm.pay_status" label="0"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="editForm.is_send" label=""></el-radio>
          <el-radio v-model="editForm.is_send" label=""></el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editOrder"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>

  import {getOrderDetailRequest, editOrderRequest} from 'network/order';

  export default {
    name: "OrderEdit",
    props: {
      orderId: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        editDialogVisible: false,
        editForm: {
          order_price: 0,
          pay_status: '0',
          is_send: ''
        },
        editFormRules: {
          order_price: [
            {required: true, message: '/', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      // 
      showBox(id) {
        getOrderDetailRequest(id).then(res => {
          let result = res.data;
          if (result.meta.status !== 200) {
            return this.alertMessage('', 'error');
          }

          this.editForm = result.data;
          this.editDialogVisible = true;
        });
      },

      // 
      editOrder() {
        let editForm = this.editForm;
        editOrderRequest(editForm.order_id, {
          order_price: editForm.order_price,
          pay_status: editForm.pay_status,
          is_send: editForm.is_send === '' ? '1' : '0'
        }).then(res => {
          let result = res.data;
          if (result.meta.status !== 201) {
            return this.alertMessage('', 'error');
          }
          this.alertMessage('', 'success');
          this.$emit('order-list');
          this.editDialogVisible = false;
        })
      },

      // 
      addressDialogClose() {
        this.$refs.editFormRef.resetFields();
      },
    }
  }
</script>

<style scoped>
  .editDialog {
    display: inline-block;
    margin-right: 5px;
  }
</style>
