import {request} from "./request";

// 
export function getOrderListRequest(params) {
  return request({
    url: '/orders',
    params
  })
}

// 
export function getOrderDetailRequest(id) {
  return request({
    url: `/orders/${id}`
  })
}

// 
export function editOrderRequest(id, data) {
  return request({
    url: `/orders/${id}`,
    method: 'put',
    data
  })
}
